import { request } from '@/utils/http'

// 获取场馆订单列表
export const getWxStadiumOrderList = (data) => {
  return request({
    url: '/infoPlatform/wxMember/orderList',
    data: data,
  })
}

//获取场馆订单详情
export const getWxStadiumOrderDetail = (data) => {
  return request({
    url: '/infoPlatform/wxMember/orderGet',
    data: data,
  })
}

// 获取场馆列表
export const getWxStadiumList = (param) => {
  return request({
    url: '/infoPlatform/wxStadium/list',
    method: 'post',
    data: param,
  })
}

